!function(){
    let view = document.querySelector('.wrapper>.sign_in>form')
    let controller = {
        view: null,
        init: function(view){
            this.view = view
            this.bindEvents()
            // this.ajaxModule()
        },
        bindEvents: function(){
            this.view.addEventListener('submit', (e)=>{
                e.preventDefault()
                let needText = {'email':'邮箱','password':'密码','confirmPassword':'确认密码'}
                let need = ['email','password','confirmPassword']
                let hash = {}
                this.clearData()
                this.gatherData(need,hash)
                this.verifyData(need,needText,hash)
            }, false)
        },
        clearData: function(){
            this.view.querySelectorAll('span[class=error]').forEach(function(value){
                value.innerHTML = ''
            })
        },
        gatherData: function(need,hash){
            need.forEach((value)=>{
                hash[value] = this.view.querySelector(`[name=${value}]`).value
            })
            return hash
        },
        verifyData: function(need,needText,hash){
            if(hash['email'].indexOf('@') === -1){
                this.view.querySelector(`[name=email]`).nextElementSibling.innerHTML = `邮箱格式不正确`
            }
            if(hash['password'] !== hash['confirmPassword']){
                this.view.querySelector('[name=confirmPassword]').nextElementSibling.innerHTML = '确认密码错误'
            }
            need.forEach((value)=>{
                if(hash[value] === ''){
                    this.view.querySelector(`[name=${value}]`).nextElementSibling.innerHTML = `请输入${needText[value]}`
                }
            })
        }
        // ajaxModule: function(){
        //     var XHR = new XMLHttpRequest()
        //     XHR.onreadystatechange = function(){}
        //     XHR.open('POST','/sign_in')
        //     XHR.send()
        // }
    }
    controller.init.call(controller,view)
}.call()